import { lazy } from 'react';
import { Routes, Route } from 'react-router-dom';

// project imports
import Loadable from 'ui-component/Loadable';

// login option 3 routing
const AuthLogin = Loadable(lazy(() => import('views/auth/auth-header/Login')));
const AuthRegister = Loadable(lazy(() => import('views/auth/auth-header/Register')));

// ==============================|| AUTHENTICATION ROUTING ||============================== //

const AuthRouter = () => {
  return (
    <>
      <Routes>
        <Route path="signin" element={<AuthLogin />} />
        <Route path="signup" element={<AuthRegister />} />
      </Routes>
    </>
  );
};

export default AuthRouter;
