import { lazy } from 'react';
import { Routes, Route } from 'react-router-dom';

// project imports
import MainLayout from 'layout/MainLayout';
import Loadable from 'ui-component/Loadable';

import DentistsPage from 'views/settings/Dentists';
import UserBaseDataPage from 'views/settings/BaseData';
import AppointmentPage from 'views/settings/Appointment';

// dashboard routing
const DashboardDefault = Loadable(lazy(() => import('views/dashboard/Default')));

// ==============================|| MAIN ROUTING ||============================== //

const MainRouter = () => {
  return (
    <MainLayout>
      <Routes>
        <Route path="dashboard" element={<DashboardDefault />} />
        <Route path="setting/users" element={<DentistsPage />} />
        <Route path="setting/basedata" element={<UserBaseDataPage />} />
        <Route path="setting/appointmenttype" element={<AppointmentPage />} />
      </Routes>
    </MainLayout>
  );
};

export default MainRouter;
